import React from "react";
import { Avatar } from "@mui/material";
import './style.css';
const Index = ({ image, profileSrc, title }) => {
  return (
    <div
      className="story"
    >
      <img src={image} className="story__image"/>
      <Avatar className="story__avatar" src={profileSrc} sx={{width: 40, height: 40}}/>
      <h4>{title}</h4>
    </div>
  );
};

export default Index;
