import React, {useState, useEffect} from 'react';
import './style.css';
import StoryReel from '../StoryReel';
import MessageSender from '../MessageSender'
import Post from '../Post'
const Index = () => {
    const [inforPost, setInforPost] = useState([
        {
            message: 'Tuan',
            imgUrl: 'https://www.pngitem.com/pimgs/m/664-6644509_icon-react-js-logo-hd-png-download.png',
            time: "Hôm kia",
            timedate: "Ngày 20 tháng 10 lúc 19:10",
            comment: [
                {
                    time: 1635004793573,
                    content: "hello Manh Tuan",
                    imgUrl : "https://photo-cms-viettimes.zadn.vn/w666/Uploaded/2021/aohuooh/2021_02_25/2-2-6272.jpeg",
                    showDelete: false
                }
            ],
            like: 0,
        }
    ]);

    const [removePost, setRemovePost] = useState(null)


    useEffect(() => {
        let CopyInforPost = [...inforPost]
        CopyInforPost = CopyInforPost.filter((item)=>item.time !== removePost)
        setInforPost(CopyInforPost)
    }, [removePost]);


    return (
        <div className="feed">
            <StoryReel />
            <MessageSender inforPost={inforPost} setInforPost={setInforPost}/>
            {
                inforPost.map((item,index)=>(
                    <Post message={item.message} time={item.time} timedate={item.timedate} imgUrl={item.imgUrl} comment={item.comment} like={item.like} setRemovePost={setRemovePost} inforPost={inforPost} setInforPost={setInforPost}/>
                ))
            }
            

        </div>
    );
}

export default Index;
